const JsyServer = require("services/jsy-server.js");
let type, _this;

export default {
  data() {
    return {
      scopesData: [[{
        item: '棉花',
        num: 1 << 0,
        active: false
      }, {
        item: '化纤',
        num: 1 << 1,
        active: false
      }, {
        item: '纱线',
        num: 1 << 2,
        active: false
      }], [{
        item: '坯布',
        num: 1 << 3,
        active: false
      }, {
        item: '印染',
        num: 1 << 4,
        active: false
      }, {
        item: '家纺',
        num: 1 << 5,
        active: false
      }], [{
        item: '色织',
        num: 1 << 6,
        active: false
      }, {
        item: '梭织',
        num: 1 << 7,
        active: false
      }, {
        item: '针织',
        num: 1 << 8,
        active: false
      }], [{
        item: '面料',
        num: 1 << 9,
        active: false
      }, {
        item: '辅料',
        num: 1 << 10,
        active: false
      }, {
        item: '服饰',
        num: 1 << 11,
        active: false
      }], [{
        item: '机械',
        num: 1 << 12,
        active: false
      }, {
        item: '配件',
        num: 1 << 13,
        active: false
      }, {
        item: '纺织厂',
        num: 1 << 14,
        active: false
      }], [{
        item: '加工厂',
        num: 1 << 15,
        active: false
      }, {
        item: '服装厂',
        num: 1 << 16,
        active: false
      }, {
        item: '其他',
        num: 1 << 17,
        active: false
      }]],
      isScope: false,
      //选择行业模态框
      scopesValue: '',
      //选中的行业
      region: '',
      //选中的城市
      contPhone: '',
      //电话号码
      contName: '',
      //姓名
      corporateName: '',
      //公司名称
      customerId: '',
      //公司id
      time: -1 //倒计时时间

    };
  },

  onLoad: function (options) {
    type = options.type;
    _this = this;
    let userInfo = wx.getStorageSync("userInfo");
    this.setData({
      contPhone: userInfo.phone,
      contName: userInfo.name
    });
  },
  components: {},
  props: {},
  methods: {
    choiceIndustry: function (e) {
      this.setData({
        industryIndex: e.detail.value
      });
    },
    bindScope: function (e) {
      if (e.currentTarget.dataset.type == 1) {
        let scopesData = this.scopesData;
        let scopesValue = "";
        scopesData.map(item => {
          item.map(items => {
            if (items.active) {
              if (scopesValue) {
                scopesValue = scopesValue + ',' + items.item;
              } else {
                scopesValue += items.item;
              }
            }
          });
        });
        this.setData({
          scopesValue: scopesValue
        });
      }

      this.setData({
        isScope: !this.isScope
      });
    },
    // 点击所选行业
    bindScopeOption: function (e) {
      let one = e.currentTarget.dataset.one,
          two = e.currentTarget.dataset.two,
          nowActive = this.scopesData[one][two].active;
      this.setData({
        [`scopesData[${one}][${two}].active`]: !nowActive
      });
    },
    // 选择城市
    bindRegionChange: function (e) {
      this.setData({
        region: e.detail.value
      });
    },
    // 提交信息
    formSubmit: function (e) {
      let data = e.detail.value;
      let code = data.code;
      data.type = type;
      data.customerId = this.customerId;
      delete data.code;

      if (!data.corporateName) {
        wx.showToast({
          title: '请选择所属公司'
        });
        return;
      } else if (!data.contName) {
        wx.showToast({
          title: '请输入姓名'
        });
        return;
      } else if (!data.contPhone) {
        wx.showToast({
          title: '请输入手机号'
        });
        return;
      } else if (!code) {
        wx.showToast({
          title: '请输入验证码'
        });
        return;
      }

      JsyServer.userRegistration(data, code).then(res => {
        wx.showToast({
          title: '注册成功'
        });
        let timer = setTimeout(() => {
          wx.setStorageSync("register", true);
          wx.switchTab({
            url: '/pages/tab-user/index'
          });
        }, 1500);
      }).catch(err => {
        wx.showToast({
          title: err.data.description,
          icon: 'none'
        });
      });
    },
    // 获取手机号
    bindTel: function (e) {
      this.setData({
        contPhone: e.detail.value
      });
    },
    // 获取验证码
    validateFn: function (e) {
      let data = {
        phone: this.contPhone
      };
      JsyServer.getVerificationCode(data).then(res => {
        wx.showToast({
          title: '发送成功',
          icon: 'none'
        });

        _this.setData({
          time: 60
        });

        _this.downTime();
      }).catch(err => {
        wx.showToast({
          title: '发送验证码失败',
          icon: 'none'
        });
      });
    },
    //倒计时
    downTime: function () {
      let backTime = _this.time;
      let backInter = setInterval(() => {
        backTime--;

        _this.setData({
          time: backTime
        });

        if (backTime <= 0) {
          clearInterval(backInter);

          _this.setData({
            time: -1
          });

          return false;
        }
      }, 1000);
    },
    toChoosingCompany: function () {
      wx.navigateTo({
        url: '/pages/jin-suo-yun/choosing-company?type=' + type
      });
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
